import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";

import { FormGroup } from "@angular/forms";
import { ChangePasswordModel } from "../../models";
import { ConfigModel } from "@soushians/config";
import { Observable } from "rxjs/Observable";

@Component({
	selector: "profile-edit",
	template: `<div fxFlex="450px">

  
  <form [formGroup]="formGroup" (ngSubmit)="editProfile()" fxLayout="column">
    <mat-card>
      <mat-card-content>
          <!-- <ngs-form-view [id]="formId"></ngs-form-view> -->
      </mat-card-content>
      <mat-card-actions fxLayoutAlign="center center">
        <button fxFlex="nogrow" type="submit" mat-raised-button color="primary"></button>
        <button fxFlex="nogrow" type="button" routerLink="/user/panel/profile" mat-raised-button></button>
      </mat-card-actions>
    </mat-card>
  </form>
</div>`
})
export class ProfileEditComponent implements OnInit {
	@Output() submited = new EventEmitter();

	@Input() formId: string;
	@Input() formGroup: FormGroup;
	@Input("roles") roles$: Observable<string[]>;
	@Input() groups: string[];
	constructor() {}

	ngOnInit() {}
	editProfile() {
		if (!this.formGroup.valid) return;
		this.submited.emit(this.formGroup.value);
	}
}
