import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { ChangePasswordModel } from "../../models";
// import { MdSnackBar } from "@angular/material";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

@Component({
        selector: 'user-change-password',
        template: `<div fxFlex="450px">
  <form [formGroup]="formGroup" (ngSubmit)="changePassword()" fxLayout="column">
    <mat-card>
      <mat-card-content>
        <mat-form-field fxFlexFill>
          <input type="password" matInput placeholder=" " formControlName="Password">
        </mat-form-field>
        <mat-form-field fxFlexFill>
          <input type="password" matInput placeholder="  " formControlName="Confirm">
        </mat-form-field>
      </mat-card-content>
      <mat-card-actions fxLayoutAlign="center center">
        <button fxFlex="nogrow" type="submit" mat-raised-button color="primary">  </button>
        <button fxFlex="nogrow" type="button" routerLink="/user/panel" mat-raised-button></button>
      </mat-card-actions>
    </mat-card>
  </form>
</div>`,
        styles: [``]
})
export class ChangePasswordComponent implements OnInit {
        @Input() formGroup: FormGroup;
        @Output() submited = new EventEmitter();

        ngOnInit() {
        }

        submit() {
        }

        constructor() { }

        changePassword() {
            if (!this.formGroup.valid) return;
            this.submited.emit(this.formGroup.value)
        }
}
